body,
html,
#root,
.app {
    width: 100%;
    height: 100%; //!!! If some div with position fixed overlapping scroll, remove height:100% from html
    margin: 0;
    padding: 0;
}

* {
    margin: 0;
    padding: 0;
    border: 0;
}

*,
*:before,
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

:focus,
:active {
    outline: none;
}

a:focus,
a:active {
    outline: none;
}

html,
body {
    font-size: 100%;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
    font-family: inherit;
}

input::-ms-clear {
    display: none;
}

button {
    cursor: pointer;
    background: transparent;
    color: inherit;

    &::-moz-focus-inner {
        padding: 0;
        border: 0;
    }
}

a,
a:visited {
    text-decoration: none;
    color: var(--default-text-color);
}

a:hover {
    text-decoration: none;
}

ul li {
    list-style: none;
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    font-weight: 400;
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    //text-indent: 1px;
    //text-overflow: '';

    &::-ms-expand {
        display: none;
    }
}
//hide spin-button in input[type=number]
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
input[type=number] {
    -moz-appearance: textfield;
}

